// Program

.program__title--main {
  @extend .program__title;
  margin-bottom: 43px;

  @media screen and (min-width: 768px) and (max-width: 1353px) {
    margin-bottom: 52px;
    text-align: left;
  }

  @media screen and (min-width: 1354px) {
    position: relative;
    margin-bottom: 160px;
    text-align: left;

    &::after {
      position: absolute;
      display: block;
      content: '';
      width: 100%;
      height: 0.5px;
      bottom: -100px;
      left: 0;
      background: $primary-bg-color;
    }
  }
}

.program {
  padding-top: 50px;
  position: relative;
  background-color: $primary-title-color;

  background-size: cover;
  background-position: left top;
  background-image: linear-gradient(
    $primary-title-color 89.8%,
    $primary-bg-color 89.8%
  );

  @media screen and (min-width: 768px) {
    padding-top: 60px;
    background-image: linear-gradient(
      $primary-title-color 84.3%,
      $primary-bg-color 84.3%
    );
  }
  @media screen and (min-width: 1354px) {
    padding-top: 120px;
    background-image: linear-gradient(
      $primary-title-color 84.1%,
      $primary-bg-color 84.1%
    );
  }

  &__container {
    position: relative;

    &::after {
      content: '';
      position: absolute;
      display: block;
      background-size: cover;
      background-repeat: no-repeat;

      @media screen and (min-width: 768px) {
        right: -20px;
        bottom: 650px;
        width: 384px;
        height: 280px;
        background-image: url(../images/program/tablet/main-pic@1x.png);

        @media screen and (min-device-pixel-ratio: 2),
          screen and (min-resolution: 192dpi),
          screen and (min-resolution: 2dppx) {
          background-image: url(../images/program/tablet/main-pic@2x.png);
        }
      }

      @media screen and (min-width: 1354px) {
        left: -150px;
        bottom: 450px;
        width: 672px;
        height: 502px;
        background-image: url(../images/program/desktop/main-pic@1x.png);

        @media screen and (min-device-pixel-ratio: 2),
          screen and (min-resolution: 192dpi),
          screen and (min-resolution: 2dppx) {
          background-image: url(../images/program/desktop/main-pic@2x.png);
        }
      }
    }
  }

  &__title {
    text-align: center;
    letter-spacing: 0.03em;
    color: $primary-bg-color;
    @include font(800, 20px, 1.3);

    @media screen and (min-width: 768px) and (max-width: 1353px) {
      @include font(800, 26px, 1.308);
    }
    @media screen and (min-width: 1354px) {
      @include font(800, 46px, 1.304);
    }
  }

  &__list {
    position: relative;
    padding-left: 100px;
    margin-bottom: 50px;

    @media screen and (max-width: 767px) {
      &::after {
        content: '';
        position: absolute;
        background-size: cover;
        background-repeat: no-repeat;

        right: -20px;
        bottom: -85px;
        width: 120px;
        height: 137px;
        background-image: url(../images/program/mobile/main-pic@1x.png);

        @media screen and (min-device-pixel-ratio: 2),
          screen and (min-resolution: 192dpi),
          screen and (min-resolution: 2dppx) {
          background-image: url(../images/program/mobile/main-pic@2x.png);
        }
      }
    }

    @media screen and (min-width: 768px) and (max-width: 1353px) {
      padding-bottom: 189px;
      padding-left: 119px;
    }
    @media screen and (min-width: 1354px) {
      padding-bottom: 164px;
      display: flex;
    }

    &::before {
      position: absolute;
      display: block;
      content: '';
      width: 1px;
      height: 1250px;
      left: 68px;
      bottom: 90px;
      background-color: $primary-bg-color;

      @media screen and (min-width: 368px) {
        height: 1230px;
      }
      @media screen and (min-width: 768px) and (max-width: 1353px) {
        left: 57px;
        bottom: 315px;
        height: 1030px;
      }
      @media screen and (min-width: 1354px) {
        top: -60px;
        width: 100%;
        height: 1px;
        left: 88px;
        display: none;
      }
    }
  }

  &__item {
    position: relative;

    &:not(:last-child) {
      margin-bottom: 30px;
    }

    @media screen and (min-width: 1354px) {
      width: 100%;
      justify-content: center;

      .program-list & + & {
        margin-left: 30px;
      }
    }

    &::before {
      content: '';
      display: block;
      position: absolute;
      align-items: center;
      justify-content: center;
      width: 12px;
      height: 12px;
      top: 7px;
      transform: translateX(-38px);
      border-radius: 50%;
      background-color: $accent-color;

      @media screen and (min-width: 768px) and (max-width: 1353px) {
        left: -30px;
        top: 7px;
      }
      @media screen and (min-width: 1354px) {
        left: 35px;
        top: -65px;
      }
    }
  }

  &__score {
    margin-bottom: 5px;
    letter-spacing: 0.08em;
    text-transform: uppercase;
    color: $primary-bg-color;
    @include font(700, 14px, 2);

    @media screen and (min-width: 768px) and (max-width: 1353px) {
      margin-bottom: 25px;
      @include font(400, 14px, 2);
    }

    @media screen and (min-width: 1354px) {
      margin-bottom: 25px;
      @include font(700, 20px, 1.4);
    }
  }

  &__meta {
    @media screen and (min-width: 768px) and (max-width: 1353px) {
      display: flex;
    }
  }

  &__content {
    &:not(:last-child) {
      margin-bottom: 8px;
    }

    @media screen and (min-width: 768px) and (max-width: 1353px) {
      width: max-content;

      .program__meta & + & {
        margin-left: 50px;
      }
      &:not(:last-child) {
        margin-bottom: 40px;
      }
      &:nth-child(1) {
        width: 176px;
      }
      &:nth-child(2) {
        width: 155px;
      }
    }

    @media screen and (min-width: 1354px) {
      &:not(:last-child) {
        margin-bottom: 25px;
      }
    }
  }

  &__name {
    margin-bottom: 5px;
    letter-spacing: 0.08em;
    color: $accent-color;
    @include font(700, 12px, 1.667);

    @media screen and (min-width: 768px) and (max-width: 1353px) {
      margin-bottom: 20px;
    }
    @media screen and (min-width: 1354px) {
      margin-bottom: 10px;
      @include font(700, 20px, 1.65);
    }
  }

  &__event {
    opacity: 0.5;
    letter-spacing: 0.08em;
    color: $primary-text-color;
    @include font(500, 12px, 1.667);

    &:not(:last-child) {
      margin-bottom: 8px;
    }

    @media screen and (min-width: 1354px) {
      margin-bottom: 10px;
      @include font(700, 18px, 1.556);
    }
  }
}

// Program-features

.program__title--features {
  @extend .program__title;
  margin-bottom: 30px;
  width: 180px;
  text-align: left;
  color: $primary-bg-color;

  @media screen and (min-width: 768px) and (max-width: 1353px) {
    width: 100%;
    text-align: center;
  }
  @media screen and (min-width: 1354px) {
    display: none;
  }
}

.program-features {
  &__list {
    position: relative;
    z-index: 100;
    padding: 53px 34px 40px 30px;
    counter-reset: number;

    letter-spacing: 0.08em;
    background-color: $primary-bg-color;
    color: $primary-title-color;
    box-shadow: -5px 8px 26px rgba(0, 0, 0, 0.04);

    @media screen and (min-width: 768px) {
      padding: 63px 118px 13px 98px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
    @media screen and (min-width: 1354px) {
      padding: 75px 62px 33px 84px;
    }
  }

  &__item {
    width: 100%;
    @include font(700, 14px, 1.43);

    @media screen and (max-width: 767px) {
      &:not(:last-child) {
        margin-bottom: 32px;
      }
    }

    @media screen and (min-width: 768px) and (max-width: 1353px) {
      padding-top: 16px;
      padding-bottom: 37px;
      width: calc((100% - 20px) / 2);
    }
    @media screen and (min-width: 1354px) {
      padding-top: 26px;
      padding-bottom: 95px;
      width: calc((100% - 120px) / 3);
      @include font(700, 20px, 1.4);
    }
  }

  &__description {
    position: relative;
    letter-spacing: 0.08em;
    @include font(700, 14px, 1.429);

    @media screen and (min-width: 768px) and (max-width: 1353px) {
      padding-top: 16px;
    }
    @media screen and (min-width: 1354px) {
      padding-top: 26px;
      width: 356px;
      @include font(700, 20px, 1.4);
    }

    &::before {
      position: absolute;
      content: '';
      display: block;
      top: -13px;
      width: 20px;
      height: 2px;
      background-color: $accent-color;

      @media screen and (min-width: 768px) and (max-width: 1353px) {
        top: -7px;
      }
      @media screen and (min-width: 1354px) {
        top: -10px;
      }
    }

    &::after {
      position: absolute;
      content: '0' counter(number);
      top: -23px;
      left: 29px;

      @include font(700, 14px, 1.71);
      color: $accent-color;
      counter-increment: number;

      @media screen and (min-width: 768px) and (max-width: 1353px) {
        top: -16px;
      }
      @media screen and (min-width: 1354px) {
        top: -26px;
        @include font(700, 20px, 1.65);
      }
    }
  }
}
